import React, { useState } from "react"
import styled, { keyframes, css } from "styled-components"
import logo from "../../images/react.svg"
import redux from "../../images/redux.svg"
import gatsby from "../../images/gatsby-icon.svg"
import htmlCss from "../../images/HtmlCss.svg"
import Bootstrap from "../../images/Bootstrap.svg"
import JavaScript from "../../images/Js.svg"
import Sass from "../../images/Sass.svg"

import { fadeInRight, fadeInDown } from "react-animations"

const spin = keyframes`
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
`

const Logos = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 80vw;
`

const RightIn = keyframes`${fadeInRight}`
const FadeUp = keyframes`${fadeInDown}`

const fadeReact = css`
  animation: 1s ${FadeUp};
`

const spinReact = css`
  animation: ${spin} infinite 15s linear;
`

const ReactLogo = styled.img`
  width: 150px;
  ${props => (props.spin ? spinReact : fadeReact)};
  margin: 10px 0;
`

const GatsbyImg = styled.img`
  animation: 2s ${FadeUp};
  width: 80px;
  margin: 10px 50px;
`

const ReduxLogo = styled.img`
  animation: 2s ${FadeUp};
  width: 100px;
  margin: 10px 50px;
`

const HtmlCss = styled.img`
  animation: 2s ${FadeUp};
  width: 120px;
  margin: 10px 10px 10px 50px;
`

const JavaScriptLogo = styled.img`
  animation: 2s ${FadeUp};
  width: 57px;
  margin: 10px 0px;
`

const BootstrapLogo = styled.img`
  animation: 2s ${FadeUp};
  width: 85px;
  margin: 10px 50px;
`

const SassLogo = styled.img`
  animation: 2s ${FadeUp};
  width: 100px;
  margin: 10px 50px;
`

const Container = styled.div`
  width: 100%;
  animation: 1s ${RightIn};
  display: flex;
  flex-wrap: wrap;
`

const Description = styled.div`
  width: 100%;
  text-align: center;
`

const skills = () => {
  const [isReact, setReact] = useState("none")
  const [isGatsby, setGatsby] = useState("none")
  const [isRedux, setRedux] = useState("none")
  const [isBootstrap, setBootstrap] = useState("none")
  const [isSass, setSass] = useState("none")
  const [isHtmlCssJs, setHtmlCssJs] = useState("none")
  const [isTimeToSpin, setSpin] = useState(false)

  const timeOut = (time, set, spin) => {
    setTimeout(() => {
      set("block")
      if (spin) {
        setSpin(true)
      }
    }, time)
  }

  timeOut(1000, setReact)
  timeOut(1200, setGatsby)
  timeOut(1400, setRedux)
  timeOut(1600, setBootstrap)
  timeOut(1800, setSass)
  timeOut(2000, setHtmlCssJs, true)

  return (
    <Container>
      <Logos>
        <Description>
          <h4>gfgf</h4>
        </Description>
        <ReactLogo
          spin={isTimeToSpin}
          display={isReact}
          alt="logo"
          src={logo}
        />
        <GatsbyImg display={isGatsby} alt="logo" src={gatsby} />
        <ReduxLogo display={isRedux} alt="logo" src={redux} />
        <BootstrapLogo display={isBootstrap} alt="logo" src={Bootstrap} />
        <SassLogo display={isSass} alt="logo" src={Sass} />
        <div
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <HtmlCss display={isHtmlCssJs} alt="logo" src={htmlCss} />
          <JavaScriptLogo display={isHtmlCssJs} alt="logo" src={JavaScript} />
        </div>
      </Logos>
    </Container>
  )
}

export default skills
